html,
body {
  height: 100%;
  line-height: 1;
  font-size: 16px;
  color: #000;
  font-family: "Quicksand", "Arial", sans-serif; }

.container {
  max-width: 1640px;
  margin: 0 auto; }

.button {
  width: 165px;
  height: 50px;
  background-color: #0063DD;
  border-radius: 25px;
  color: #FFF;
  text-transform: uppercase;
  line-height: 20px;
  cursor: pointer; }
  .button:hover {
    border: 1px solid #0063DD;
    background: #FFFFFF;
    color: #0063DD; }

.page-header {
  position: relative;
  background-image: url("../img/background.svg");
  background-repeat: no-repeat;
  background-size: 1920px 1043px;
  background-position: 50% 0; }
  .page-header__container {
    display: flex;
    min-height: 100px;
    align-items: center; }
  .page-header__navigation {
    margin: 0 0 0 893px; }

.navigation__button {
  width: 165px;
  height: 50px;
  background: #0063DD;
  border-radius: 25px;
  margin-left: 52px;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 20px; }

.navigation__logo:hover {
  box-shadow: 0 4px 16px rgba(0, 66, 148, 0.08); }

.menu {
  display: flex; }
  .menu__item {
    font-weight: 500;
    line-height: 20px;
    text-transform: uppercase;
    color: #929292; }
  .menu__item:not(:last-child) {
    margin: 0 42px 0 0; }

.info__container {
  padding: 88px 0 594px;
  background-image: url("../img/Devices.png");
  background-repeat: no-repeat;
  background-size: 804px 918px;
  background-position: 50% 352px; }

.info__title {
  margin: 0 0 30px;
  text-align: center;
  font-weight: 700;
  font-size: 36px;
  line-height: 45px; }

.info__text {
  width: 655px;
  margin: 0 auto 30px;
  text-align: center;
  font-size: 16px;
  line-height: 27px;
  color: #929292; }

.info__buttons {
  margin: 0 auto;
  width: 362px;
  display: flex;
  align-items: center;
  justify-content: space-between; }

.scroll-down {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: -57px;
  width: 114px;
  height: 114px;
  background: rgba(0, 99, 221, 0.23);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer; }
  .scroll-down:hover {
    box-shadow: 0 4px 16px rgba(0, 66, 148, 0.08); }
  .scroll-down__button {
    width: 70px;
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #0063DD;
    border-radius: 50%;
    cursor: pointer; }

.services {
  background: #F5FCFD; }
  .services__container {
    padding: 158px 250px 137px; }
  .services__list {
    display: flex;
    align-items: center;
    justify-content: space-between; }
  .services__item {
    width: 362px;
    height: 290px;
    padding: 40px 15px 47px;
    background: #FFFFFF;
    box-shadow: 0 4px 16px rgba(0, 66, 148, 0.08);
    border-radius: 5px; }
    .services__item:nth-child(2) {
      margin-top: 50px; }
  .services__icon {
    display: block;
    margin: 0 auto 25px; }
  .services__title {
    margin: 0 0 25px;
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    color: #000000;
    text-align: center; }
  .services__content {
    font-weight: 300;
    font-size: 16px;
    line-height: 20px;
    text-align: center;
    color: #000000; }
